@media print {
  body {
    margin-bottom: 24px;
  }
  a[data-print]::after {
    content: " (" attr(data-print) ")";
  }

  .page-break {
    page-break-after: always;
  }
  section {
    margin: 48px 0;
  }

  footer ul li {
    display: block;
  }
  header {
    background: #aa076b;
    height: 300px;
    min-height: 0;
  }
  .block + .block {
    margin-top: 1em;
  }
  h3,
  h4 {
    font-size: 20px;
    margin-bottom: 12px;
    line-height: 1.2;
  }
  p {
    font-size: 18px;
  }
  .download,
  .header-links {
    display: none;
  }
}
